<template>
  <div class="progres-bar-container">
    <div class="progres-bar-content">
      <h1>   </h1>
      <div class="progres-bar-wrapper">
        <div class="progres-header">
          <progres-bar-items
            :headers="progress_items"
            @changedTab="updateTab($event)"
          >
          </progres-bar-items>
        </div>
        <div class="progres-content">
          <div class="progres-content-left">
            <p :class="{ active: event == 'one' || default_class }"> </p>
            <p :class="{ active: event == 'two' }"> </p>
            <p :class="{ active: event == 'three' }">  </p>
            <p> </p>
          </div>
          <div class="progres-content-right">
            <span
              >     
              ?</span
            >
            <hr>
            <span
              >       . 
                ?</span
            >
            <hr>
            <span
              >    ,    
                 ?</span
            >
            <hr>
            <span
              >    50%     
                   ?</span
            >
            <hr>
            <span
              >    ,     
                      ?</span
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ProgresBarItems from "./content-items/ProgresBarItems.vue";
export default {
  name: "progres-bar",
  components: { ProgresBarItems },
  data() {
    return {
      progress_items: [
        {
          name: "Active",
          title: "  ",
          value: "one",
          active: true,
        },
        {
          name: "Inactive",
          title: " ",
          value: "two",
        },
        {
          name: "Inactive",
          title: "  ",
          value: "three",
        },
      ],
      event: "",
      default_class: true
    };
  },
  methods: {
    updateTab(event) {
      this.event = event;
      this.default_class = false
    },
  },
};
</script>
